.caroussel{
    position: relative;
    display: flex;
    align-items: center;
    width: 100%;
    height: 25.9375em;
    margin: 0 auto;
}

.caroussel img{
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 1.5625em;
}

.caroussel input{
    position: absolute;
    z-index: 1;
    width: 4.95em;
}

.caroussel input:first-child{
    left: 1.46em;
    transform: rotate(-90deg);
}

.caroussel input:last-child{
    right: 1.46em;
    transform: rotate(90deg);
}

.animator{
    opacity: 1;
    transition: opacity .4s;
}

.imgcount{
    position: absolute;
    top: 1em;
    right: 2em;
    z-index: 1;
    padding: .4em .8em;
    border-radius: 100vw;
    color: white;
    background-color: black;
}

@media screen  and (max-width: 720px){
    
    .caroussel{
        height: 30.6306em;
    }

    .caroussel input{
        width: 3.112em;
    }

    .imgcount{
        font-size: 1.2em;
        top: 0.8em;
        right: 1.6em;
    }

}