header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 2.5em 5.208vw 0;
}

header nav{
    display: flex;
    justify-content: space-between;
    width: 17em;
}

header img{
    width: 13.1445em;
}

header a{
    color: #FF6060;
    text-decoration: none;
    font-size: 1.5em;
}

header .underline{
    animation: underline .3s forwards;
}

@keyframes underline {
    0% {text-decoration: underline .07em #FF606000;}
    100% {text-decoration: underline .07em #FF6060;}
}

@media screen and (max-width: 720px){
    
    header img{
        width: 19.5em;
    }

    header nav{
        width: 15em;
    }

}